<template>
    <v-btn width="100%" height="60px" color="success" dark @click="naverLogin">NAVER  
    </v-btn>
</template>

<script>
export default {
    methods:{
        naverLogin(){
            this.$http.post('/api/login/naver', {p:this.$router.history.current.path}).then(res => {
                location.href = res.data.result;
            }).catch(err=>{
                alert('      ')
            })
        }
    }
}
</script>